export const getGradientColor = (value, min, max, crypto=false) => {
    const rgbToHex = (r, g, b) => {
        return "#" + ((1 << 24) + (r << 16) + (g << 8) + b).toString(16).slice(1);
    }
    let col1 = [144,218,185]  // #90DAB9
    let col2 = [49,71,106]  // #31476A
    let soft = number => Math.log(number)

    if (crypto) {
        col1 = [0, 81, 255]  // #0051ff
        col2 = [0, 0, 0]     // #000000
        soft = number => Math.log(number)
    }
    const w1 = (soft(value) - soft(min)) / (soft(max) - soft(min))
    const w2 = 1 - w1
    const rgb = [Math.round(col1[0] * w1 + col2[0] * w2),
        Math.round(col1[1] * w1 + col2[1] * w2),
        Math.round(col1[2] * w1 + col2[2] * w2)];
    return rgbToHex(...rgb)
}
const strDateToNumber = (strDate) => {
    const day = 24 * 60 * 60 * 1000
    const year = 365

    let aYearAgo = new Date()
    aYearAgo = new Date(aYearAgo.getTime() - (year * day))
    aYearAgo.setHours(0, 0, 0, 0)

    let d = new Date(strDate)
    d.setHours(0, 0, 0, 0)
    return Math.round(((d.getTime() - aYearAgo.getTime()) / day))
}
const getTimeSeries = (valueHistory, amountHistory) => {
    const values = {}
    valueHistory.forEach(stock => {
        values[strDateToNumber(stock.date)] = stock.open
    })
    const amounts = {}
    amountHistory.forEach(stock => {
        amounts[strDateToNumber(stock.date)] = stock.amount
    })

    const days = [...Array(365).keys()]
    return days.map(day => {
        let value = 0
        let amount = 0

        if(day in values) {
            value = values[day]
        } else {
            for (let d = day-1; d >= 0; d--) {
                if(d in values) {
                    value = values[d]
                    break
                }
            }
        }

        if(day in amounts) {
            amount = amounts[day]
        } else {
            for (let d = day-1; d >= 0; d--) {
                if(d in amounts) {
                    amount = amounts[d]
                    break
                }
            }
        }

        return [ day, amount * value ]
    })
}
export const getFormattedData = (finData) => {
    finData.stocks = finData.stocks.filter(stock => {
        return (stock.name
            && stock.valueHistory
            && stock.valueHistory.length > 0
            && stock.amountHistory
            && stock.amountHistory.length > 0)
    })
    // slice(-1).pop() returns last element of the list
    const stocks = finData.stocks.map(stock => ({
        'name': stock.name,
        'value': stock.valueHistory.slice(-1).pop().open,
        'amount': stock.amountHistory.slice(-1).pop().amount,
        'timeSeries': getTimeSeries(stock.valueHistory, stock.amountHistory)
    }))

    stocks.sort((stockA, stockB) => {
        return (stockB.value * stockB.amount) - (stockA.value * stockA.amount)
    })

    const currentPortValue = stocks
        .map(stock => stock.value * stock.amount)
        .reduce((stockA, stockB) => stockA + stockB)
        .toFixed(2)

    const maxStockValue = stocks[0].value * stocks[0].amount
    const minStockValue = stocks.slice(-1).pop().value * stocks.slice(-1).pop().amount

    const stockData = stocks.map(stock => ({
        'title': stock.name,
        'value': (stock.value * stock.amount).toFixed(2),
        'amount': stock.amount,
        'singleValue': stock.value.toFixed(2),
        'color':  getGradientColor(stock.value * stock.amount, minStockValue, maxStockValue),
        'timeSeries': stock.timeSeries
    }))

    const pieData = [...stockData]
    pieData.forEach(stock => stock['value'] = parseFloat(stock['value']))

    const approachingMin = approachingStocks.slice(-1).pop().value
    const approachingData = approachingStocks.map(stock => ({
        'title': stock.name,
        'value': stock.value,
        'color': getGradientColor(stock.value, approachingMin, 100)
    }))

    return { currentPortValue, pieData, stockData, approachingData }
}

export const getFormattedCryptoData = (cryptoData) => {
    const cryptos = cryptoData["info"].filter(crypto => crypto.symbol !== "EUR").filter(crypto => crypto.price * crypto.available > 1)
    cryptos.sort((cryptoA, cryptoB) => {
        return (cryptoB.price * cryptoB.available) - (cryptoA.price * cryptoA.available)
    })
    const maxCryptoValue = cryptos[0].price * cryptos[0].available
    const minCryptoValue = cryptos.slice(-1).pop().price * cryptos.slice(-1).pop().available
    const cryptoPieData = cryptos.map(crypto => ({
        'title': crypto.symbol,
        'value': crypto.price * crypto.available,
        'color':  getGradientColor(crypto.price * crypto.available, minCryptoValue, maxCryptoValue, true)
    }))

    const currentCryptoValue = cryptos
        .map(crypto => crypto.price * crypto.available)
        .reduce((cryptoA, cryptoB) => cryptoA + cryptoB)
        .toFixed(2)

    return { currentCryptoValue, cryptoPieData }
}

